body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, * {
  box-sizing: border-box;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.App-header {
  width: 100%;
  padding: 20px;
  font-size: 24px;
  background-color: grey;
  color: white;
  text-align: center;
  letter-spacing: 3px;
}

.App main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.CircleSelector {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    height: 200px;
    width: 150px;
    border: 4px solid purple;
    border-radius: 8px;
    margin-right: 20px;
}
  
.CircleSelector button {
    height: 30px;
    width: 125px;
    background-color: purple;
    color: white;
    border-radius: 4px;
    text-transform: uppercase;
    cursor: pointer;
    outline: none;
}
  
.CircleSelector button:hover {
    background-color: limegreen;
}
  
/* Adding a class of 'selected' to the selected button dynamically is secret sauce */
.CircleSelector button.selected {
    background-color: white;
    color: limegreen;
    border: none;
    cursor: default;
}
.Circles {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    height: 200px;
    width: 200px;
    border: 4px solid limegreen;
    border-radius: 8px;
}
  
.Circles div {
    height: 75px;
    width: 75px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: limegreen;
    color: white;
    font-size: 30px;
    border: 5px solid limegreen;
    border-radius: 50%;
    margin: auto 10px;
}
  
/* Another dose of dynamic className goodness is the winning ticket */
.Circles div.selected {
    background-color: white;
    color: purple;
    border: 5px solid purple;
}
